.hamburger {
  position: relative;
  transition-duration: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.hamburger .bar {
  transition-duration: 0.5s;
  position: relative;
  height: 3px;
  width: 25px;
  background-color: #ffffff;
}
@media only screen and (min-width: 36em) {
  .hamburger .bar {
    transition-duration: 0.5s;
    position: relative;
    height: 3px;
    width: 30px;
    background-color: #ffffff;
  }
}
.hamburger .bar::before {
  content: "";
  top: -8px;
  transition-duration: 0.5s;
  position: absolute;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
}
@media only screen and (min-width: 36em) {
  .hamburger .bar::before {
    content: "";
    top: -9px;
    transition-duration: 0.5s;
    position: absolute;
    width: 30px;
    height: 3px;
    background-color: #ffffff;
  }
}
.hamburger .bar::after {
  content: "";
  top: 8px;
  transition-duration: 0.5s;
  position: absolute;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
}
@media only screen and (min-width: 36em) {
  .hamburger .bar::after {
    content: "";
    top: 9px;
    transition-duration: 0.5s;
    position: absolute;
    width: 30px;
    height: 3px;
    background-color: #ffffff;
  }
}
.hamburger .bar-white {
  background: #050505;
}
.hamburger .bar-white::before, .hamburger .bar-white::after {
  background: #050505;
}
.hamburger .bar-white.sticky {
  background-color: #ffffff;
}
.hamburger .bar-white.sticky::before, .hamburger .bar-white.sticky::after {
  background-color: #ffffff;
}
.hamburger .menu__text-1 {
  display: none;
  transition: 0.5s ease-out ease;
}
@media only screen and (min-width: 48em) {
  .hamburger .menu__text-1 {
    display: block;
  }
}
.hamburger .menu__text-2 {
  margin-top: 0 !important;
  display: none;
  transition: 0.5s ease-out ease;
}
.hamburger.open {
  z-index: 999;
}
.hamburger.open .bar {
  transition-duration: 0.5s;
  background: transparent;
}
.hamburger.open .bar::before {
  transform: rotateZ(45deg) scaleX(1) translate(6px, 6px);
}
.hamburger.open .bar::after {
  transform: rotateZ(-45deg) scaleX(1) translate(6px, -6px);
}
.hamburger.open .menu__text-1 {
  display: none;
  transition: 0.5s ease-out ease;
}
@media only screen and (min-width: 48em) {
  .hamburger.open .menu__text-2 {
    display: block;
    transition: 0.5s ease-out ease;
  }
}
.hamburger:hover {
  cursor: pointer;
}

