.loader {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100Vh;
  background: #f1b61d;
  color: #050505;
  z-index: 9999;
  padding: 1em 2em;
  overflow: hidden;
  display: none;
}
.loader__wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
}
.loader__wrapper::before {
  content: "Loading";
  position: absolute;
  font-size: 10em;
  font-weight: 900;
  opacity: 0.6;
  color: #050505;
}
.loader__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loader__percentWrapper {
  position: absolute;
  bottom: 60px;
  right: 100px;
  display: flex;
}
.loader__count {
  font-size: 7.5rem;
  font-family: "productSans-medium", sans-serif;
  position: relative;
}
.loader__count::after {
  content: "%";
  position: absolute;
  font-size: 4rem;
}
.loader__top img {
  animation: sliding 4s linear infinite;
  -webkit-animation: sliding 4s linear infinite;
  transition: sliding 0.4s ease-in-out;
}
@keyframes sliding {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(50px);
  }
  100% {
    transform: translateX(0);
  }
}

.page-loader {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100vh;
  z-index: 9999999;
  display: block;
}

.content {
  width: 100%;
  height: 100%;
  background-color: #f1b61d;
  display: flex;
  align-items: center;
  justify-content: center;
}
.content .loading {
  width: 80px;
  height: 50px;
  position: relative;
}
.content .loading p {
  top: 0;
  padding: 0;
  margin: 0;
  color: #050505;
  animation: text 3.5s ease both infinite;
  font-size: 20px;
  letter-spacing: 1px;
}
@keyframes text {
  0% {
    letter-spacing: 1px;
    transform: translateX(0px);
  }
  40% {
    letter-spacing: 2px;
    transform: translateX(26px);
  }
  80% {
    letter-spacing: 1px;
    transform: translateX(32px);
  }
  90% {
    letter-spacing: 2px;
    transform: translateX(0px);
  }
  100% {
    letter-spacing: 1px;
    transform: translateX(0px);
  }
}
.content .loading span {
  background-color: #050505;
  border-radius: 50px;
  display: block;
  height: 16px;
  width: 16px;
  bottom: 0;
  position: absolute;
  transform: translateX(64px);
  animation: loading 3.5s ease both infinite;
}
.content .loading span:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border-radius: inherit;
  animation: loading2 3.5s ease both infinite;
}
@keyframes loading {
  0% {
    width: 16px;
    transform: translateX(0px);
  }
  40% {
    width: 100%;
    transform: translateX(0px);
  }
  80% {
    width: 16px;
    transform: translateX(64px);
  }
  90% {
    width: 100%;
    transform: translateX(0px);
  }
  100% {
    width: 16px;
    transform: translateX(0px);
  }
}
@keyframes loading2 {
  0% {
    transform: translateX(0px);
    width: 16px;
  }
  40% {
    transform: translateX(0%);
    width: 80%;
  }
  80% {
    width: 100%;
    transform: translateX(0px);
  }
  90% {
    width: 80%;
    transform: translateX(15px);
  }
  100% {
    transform: translateX(0px);
    width: 16px;
  }
}

